/**
 * WordPress dependencies
 */
import { registerStore } from '@wordpress/data';

/**
 * Internal dependencies
 */
import { STORE_NAME } from './constant';

export const DEFAULT_STATE = {
	feedbackText: '',
	editorPreferenceModal: false,
	score: 0,
	workflow: 'default',
};

export const actions = {
	onNegativeFeedback: ( feedback ) => ( { type: 'NEGATIVE_FEEDBACK', feedback } ),
	onResetEditorPreferenceModal: () => ( { type: 'RESET_EDITOR_PREFERENCE_MODAL' } ),
	toggleEditorPreferenceModal: () => ( { type: 'TOGGLE_EDITOR_PREFERENCE_MODAL' } ),
	setFeedbackText: ( feedbackText ) => ( { type: 'SET_FEEDBACK_TEXT', feedbackText } ),
};

export const reducer = ( state = DEFAULT_STATE, action ) => {
	switch ( action.type ) {
		case 'NEGATIVE_FEEDBACK': {
			return {
				...state,
				...action.feedback, // comment, score
				editorPreferenceModal: true,
				workflow: 'feedback',
			};
		}
		case 'RESET_EDITOR_PREFERENCE_MODAL' : {
			return {
				...DEFAULT_STATE,
			};
		}
		case 'TOGGLE_EDITOR_PREFERENCE_MODAL': {
			return {
				...state,
				editorPreferenceModal: ! state.editorPreferenceModal,
			};
		}
		case 'SET_FEEDBACK_TEXT': {
			return {
				...state,
				feedbackText: action.feedbackText,
			};
		}
	}

	return state;
};

export const selectors = {
	getEditorPreferenceFeedbackText: ( state ) => state.feedbackText || '',
	getEditorPreferenceModalScore: ( state ) => state.score || 0,
	getEditorPreferenceModalWorkflow: ( state ) => state.workflow || 'default',
	isEditorPreferenceModalVisible: ( state ) => state.editorPreferenceModal || false,
};

const store = registerStore( STORE_NAME, {
	reducer,
	actions,
	selectors,
} );

export default store;
