/**
 * WordPress dependencies
 */
import { Modal } from '@wordpress/components';
import { registerPlugin } from '@wordpress/plugins';

/**
 * Internal dependencies
 */
import './store';
import './style.scss';
import ButtonsBar from './buttons-bar';
import EditorPreferenceModalFooter from './footer';
import useWorkflow from './hooks/useWorkflow';

export const EditorPreferenceModal = () => {
	const { step, button, onClose } = useWorkflow();

	if ( ! step ) {
		return null;
	}

	return (
		<Modal
			className={ `nextgen-modal editor-preference-modal editor-preference-modal--${ step.slug }` }
			onRequestClose={ onClose }
		>
			{ step.title &&
				<h2 className="nextgen-modal__title">
					{ step.title }
				</h2>
			}

			{ step.content }

			<ButtonsBar
				buttons={ step.buttons }
				selectedValue={ button.value }
				callback={ button.callback }
			/>

			<EditorPreferenceModalFooter actions={ step.actions } />
		</Modal>
	);
};

/* istanbul ignore next */
registerPlugin( 'nextgen-editor-preference-modal', {
	render: () => ( <EditorPreferenceModal /> ),
} );
