/* global nextgenEditorPreferenceModalData */

/**
 * WordPress dependencies
 */
import { useCallback } from '@wordpress/element';
import { useSelect } from '@wordpress/data';

/**
 * Internal dependencies
 */
import { STORE_NAME } from '../constant';

export default function useSendFeedback() {
	/* istanbul ignore next */
	const { initialScore, feedbackText } = useSelect( ( select ) => ( {
		initialScore: select( STORE_NAME ).getEditorPreferenceModalScore(),
		feedbackText: select( STORE_NAME ).getEditorPreferenceFeedbackText(),
	} ), [] );

	return useCallback( ( switchEditorChoice = null ) => {
		// Only send feedback if the user provided some insights (scoring, comment or opt out option)
		if ( switchEditorChoice || initialScore > 0 || feedbackText.length > 0 ) {
			const apiUrl = nextgenEditorPreferenceModalData.feedback_api;
			const bodyData = nextgenEditorPreferenceModalData.feedback_body;

			const body = JSON.stringify( {
				...bodyData,
				switch_editor: switchEditorChoice,
				comment: feedbackText,
				domain: window.location.hostname,
				score: initialScore,
			} );

			const requestOptions = {
				body,
				headers: { 'Content-Type': 'application/json' },
				method: 'POST',
			};

			fetch( apiUrl, requestOptions )
				.then( async ( response ) => {
					const data = await response.json();

					if ( ! response.ok ) {
						const error = ( data && data.message ) || response.status;

						return Promise.reject( error );
					}
				} );
		}
	}, [ feedbackText, initialScore ] );
}
