/**
 * WordPress dependencies
 */
import { Button } from '@wordpress/components';

const EditorPreferenceModalFooter = ( props ) => {
	const { actions } = props;

	if ( ! actions || actions.length < 1 ) {
		return null;
	}

	const renderButton = ( button, type ) => {
		if ( ! button ) {
			return null;
		}

		return (
			<Button
				className={ `editor-preference-modal__footer__button editor-preference-modal__footer__button--${ type }` }
				isLink={ type !== 'next' }
				isPrimary={ type === 'next' }
				disabled={ button.isDisabled }
				onClick={ button.action }>
				{ button.label }
			</Button>
		);
	};

	return (
		<footer className="editor-preference-modal__footer">
			{ renderButton( actions?.back, 'back' ) }
			{ renderButton( actions?.skip, 'skip' ) }
			{ renderButton( actions?.next, 'next' ) }
		</footer>
	);
};

export default EditorPreferenceModalFooter;
