/**
 * WordPress dependencies
 */
import { __ } from '@wordpress/i18n';

/**
 * Internal dependencies
 */
import defaultWorkflow from './default';

export const feedbackWorkflow = ( props ) => {
	const defaultWorkflowSteps = defaultWorkflow( props );

	return {
		intro: {
			...defaultWorkflowSteps.intro,
			content: (
				<>
					<p>{ __( 'We appreciate your feedback and want to make sure you have the right tools to build your site. You are currently using the GoDaddy Site editor, which gives you all of WordPress plus a way to customize your design, edit your pages, and manage your pages, all from one place.', 'nextgen' ) }</p>
					<h3
						className="nextgen-modal__subtitle">{ __( 'Would you like to keep building your site with the GoDaddy Site Editor?', 'nextgen' ) }</h3>
				</>
			),
			actions: {
				next: {
					label: __( 'Continue', 'nextgen' ),
					action: () => {
						if ( props.selectedButton === 'keepEditor' ) {
							props.sendFeedback();
							props.closeEditorPreferenceModal();
						} else {
							props.setStep( 'options' );
						}
					},
				},
			},
		},

		// List available options
		options: {
			...defaultWorkflowSteps.options,
			actions: {
				...defaultWorkflowSteps.options.actions,
				back: {
					label: __( 'Go Back', 'nextgen' ),
					action: () => props.setStep( 'intro' ),
				},
			},
		},

		// Display the Default option (WordPress)
		optionDefault: {
			...defaultWorkflowSteps.optionDefault,
		},

		// Display the 3rd party plugins option
		option3rdParty: {
			...defaultWorkflowSteps.option3rdParty,
		},

		// Display the Chat option
		optionTalk: {
			...defaultWorkflowSteps.optionTalk,
		},

		// Redirecting to Default (WordPress)
		redirectingDefault: {
			...defaultWorkflowSteps.redirectingDefault,
		},

		// Redirecting to 3rd party plugins
		redirecting3rdParty: {
			...defaultWorkflowSteps.redirecting3rdParty,
		},
	};
};

export default feedbackWorkflow;
