/**
 * External dependencies
 */
import classnames from 'classnames';

const ButtonsBar = ( props ) => {
	const { buttons, selectedValue, callback } = props;

	if ( ! buttons || buttons.length < 1 ) {
		return null;
	}

	return (
		<div className="buttons-bar">
			{ buttons.map( ( button ) => (
				<button
					key={ `buttons-bar__button-${ button.value }` }
					onClick={ () => callback( button.value ) }
					className={ classnames(
						'buttons-bar__button',
						`buttons-bar__button--${ button.value }`,
						{ 'is-selected': button.value === selectedValue }
					) }>
					{ button.icon }
					{ button.label }
				</button>
			) ) }
		</div>
	);
};

export default ButtonsBar;
