/**
 * WordPres Dependencies
 */
import { useDispatch, useSelect } from '@wordpress/data';
import { useEffect, useState } from '@wordpress/element';

/**
 * Internal Dependencies
 */
import defaultWorkflow from '../workflows/default';
import feedbackWorkflow from '../workflows/feedback';
import useSendFeedback from '../hooks/useSendFeedback';
import { STORE_NAME } from '../constant';

export default function useWorkflow( { firstStep = 'intro' } = {} ) {
	/* istanbul igonre next */
	const {
		isVisible,
		workflow,
		feedbackText,
	} = useSelect( ( select ) => ( {
		isVisible: select( STORE_NAME ).isEditorPreferenceModalVisible(),
		workflow: select( STORE_NAME ).getEditorPreferenceModalWorkflow(),
		feedbackText: select( STORE_NAME ).getEditorPreferenceFeedbackText(),
	} ), [] );

	const {
		onResetEditorPreferenceModal,
		toggleEditorPreferenceModal,
		setFeedbackText,
	} = useDispatch( STORE_NAME );

	const [ currentStep, setStep ] = useState( firstStep );
	const [ selectedButton, selectButton ] = useState( 'keepEditor' );
	const [ redirecting, setRedirecting ] = useState( null );
	const sendFeedback = useSendFeedback();

	const resetValues = () => {
		setStep( firstStep );
		selectButton( 'keepEditor' );
		setFeedbackText( '' );
		setRedirecting( null );
		onResetEditorPreferenceModal();
	};

	const closeEditorPreferenceModal = () => {
		toggleEditorPreferenceModal();
		resetValues();
	};

	useEffect( () => {
		if ( redirecting ) {
			setTimeout( () => {
				window.location.href = redirecting;
			}, 4000 );
		}
	}, [ redirecting ] );

	if ( ! isVisible ) {
		return {};
	}

	const workflowProps = {
		closeEditorPreferenceModal,
		selectedButton,
		setFeedbackText,
		sendFeedback,
		selectButton,
		setStep,
		feedbackText,
		setRedirecting,
	};

	const STEPS = workflow === 'feedback' ? feedbackWorkflow( workflowProps ) : defaultWorkflow( workflowProps );

	return {
		step: {
			slug: currentStep,
			...STEPS[ currentStep ],
		},
		onClose: () => {
			sendFeedback();
			closeEditorPreferenceModal();
		},
		button: {
			value: selectedButton,
			callback: selectButton,
		},
	};
}
