/* global nextgenEditorPreferenceModalData */

/**
 * WordPress dependencies
 */
import { __ } from '@wordpress/i18n';

/**
 * Internal dependencies
 */
import * as SVGS from './../svgs';
import { SVGs as COMMON_SVGS } from '@wpex/nextgen-common';

const defaultWorkflow = ( props ) => ( {
	intro: {
		title: __( 'Would you like to keep using the GoDaddy Site Editor?', 'nextgen' ),
		content: (
			<>
				<p>{ __( 'We want to make sure you have the right tools to build your site. You are currently using the GoDaddy Site editor, which gives you all of WordPress plus a way to customize your design, edit your pages, and manage your pages, all from one place.', 'nextgen' ) }</p>
				<h3
					className="nextgen-modal__subtitle">{ __( 'Would you like to keep building your site with the GoDaddy Site Editor?', 'nextgen' ) }</h3>
			</>
		),
		buttons: [
			{
				value: 'keepEditor',
				label: __( 'Yes, keep using GoDaddy Site Editor', 'nextgen' ),
				icon: SVGS.SVG_KEEP_EDITOR,
			},
			{
				value: 'changeEditor',
				label: __( 'No, Id like to see other options', 'nextgen' ),
				icon: SVGS.SVG_CHANGE_EDITOR,
			},
		],
		actions: {
			next: {
				label: __( 'Continue', 'nextgen' ),
				action: () => props.selectedButton === 'keepEditor' ? props.closeEditorPreferenceModal() : props.setStep( 'feedback' ),
			},
		},
	},

	// Display a feedback textarea if user is about to change
	feedback: {
		title: __( 'Were sorry this wasnt the right fit for you', 'nextgen' ),
		content: (
			<>
				<h3 className="nextgen-modal__subtitle">{ __( 'Could you tell us why?', 'nextgen' ) }</h3>
				<textarea
					className="editor-preference-modal__textarea"
					placeholder={ __( 'Tell us why this wasnt the right fit for you', 'nextgen' ) }
					autoFocus={ true } // eslint-disable-line jsx-a11y/no-autofocus
					value={ props.feedbackText }
					onChange={ ( event ) => props.setFeedbackText( event.target.value ) }>
				</textarea>
			</>
		),
		actions: {
			back: {
				label: __( 'Go Back', 'nextgen' ),
				action: () => {
					props.selectButton( 'keepEditor' );
					props.setStep( 'intro' );
				},
			},
			skip: {
				label: __( 'Skip', 'nextgen' ),
				action: () => {
					props.setStep( 'options' );
					props.selectButton( 'optionDefault' );
				},
			},
			next: {
				label: __( 'Continue', 'nextgen' ),
				action: () => {
					props.setStep( 'options' );
					props.selectButton( 'optionDefault' );
				},
				isDisabled: ( ! props.feedbackText || props.feedbackText.length < 1 ),
			},
		},
	},

	// List available options
	options: {
		title: __( 'Lets find you a better fit', 'nextgen' ),
		content: <h3
			className="nextgen-modal__subtitle">{ __( 'Here are some other options to explore:', 'nextgen' ) }</h3>,
		buttons: [
			{
				value: 'optionDefault',
				label: __( 'Switch to the default WordPress editor', 'nextgen' ),
				icon: SVGS.SVG_OPTION_DEFAULT,
			},
			{
				value: 'option3rdParty',
				label: __( 'Switch to a 3rd party page builder', 'nextgen' ),
				icon: SVGS.SVG_OPTION_3RD_PARTY,
			},
			{
				value: 'optionTalk',
				label: __( 'Talk to someone about my options', 'nextgen' ),
				icon: SVGS.SVG_OPTION_TALK,
			},
		],
		actions: {
			back: {
				label: __( 'Go Back', 'nextgen' ),
				action: () => props.setStep( 'feedback' ),
			},
			next: {
				label: __( 'Learn More', 'nextgen' ),
				action: () => {
					props.setStep( props.selectedButton );
				},
			},
		},
	},

	// Display the Default option (WordPress)
	optionDefault: {
		title: __( 'Switching to the default WordPress editor', 'nextgen' ),
		content: (
			<div className="left-aligned">
				<p>{ __( 'You should switch to the default WordPress editor if:', 'nextgen' ) }</p>
				<ul>
					<li>{ __( 'You want to use a different theme', 'nextgen' ) }</li>
					<li>{ __( 'You are familiar with and/or prefer to use default WordPress', 'nextgen' ) }</li>
				</ul>
				<div className="nextgen-modal__warning">
					{ COMMON_SVGS.SVG_WARNING }
					<p>{ __( 'Once you switch, this site will no longer work with the GoDaddy Site Editor, so you wont be able to come back.', 'nextgen' ) }</p>
				</div>
				<p>{ __( 'Ready to switch this site to the default WordPress editor?', 'nextgen' ) }</p>
			</div>
		),
		actions: {
			back: {
				label: __( 'Go Back', 'nextgen' ),
				action: () => props.setStep( 'options' ),
			},
			next: {
				label: __( 'Switch to Default', 'nextgen' ),
				action: () => {
					props.sendFeedback( 'default' );
					props.setStep( 'redirectingDefault' );
					props.setRedirecting( `${ nextgenEditorPreferenceModalData.nextgen_urls.admin }?nextgen=0` );
				},
			},
		},
	},

	// Display the 3rd party plugins option
	option3rdParty: {
		title: __( 'Switching to a 3rd Party Page Builder', 'nextgen' ),
		content: (
			<div className="left-aligned">
				<p>{ __( 'A 3rd party page builder is a WordPress plugin that you install on your site, that allows you to build and design pages.', 'nextgen' ) }</p>
				<div className="nextgen-modal__warning">
					{ COMMON_SVGS.SVG_WARNING }
					<p>{ __( 'Once you switch, this site will no longer work with the GoDaddy Site Editor, so you wont be able to come back.', 'nextgen' ) }</p>
				</div>
				<p>{ __( 'Ready to switch this site to a 3rd party page builder?', 'nextgen' ) }</p>
			</div>
		),
		actions: {
			back: {
				label: __( 'Go Back', 'nextgen' ),
				action: () => props.setStep( 'options' ),
			},
			next: {
				label: __( 'View Plugins', 'nextgen' ),
				action: () => {
					props.sendFeedback( 'third_party' );
					props.setStep( 'redirecting3rdParty' );
					props.setRedirecting( `${ nextgenEditorPreferenceModalData.nextgen_urls.admin }plugin-install.php?s=page-builder&tab=search&type=term&nextgen=0` );
				},
			},
		},
	},

	// Display the Chat option
	optionTalk: {
		content: (
			<div className="editor-preference-modal__content-contact">
				<h2 className="nextgen-modal__title">
					<span>{ __( 'Expert help is on the way', 'nextgen' ) }</span>
				</h2>
				<button onClick={ () => {
					props.sendFeedback( 'help' );
					props.closeEditorPreferenceModal();
					window.open( 'https://www.godaddy.com/help/contact-us', '_blank', 'noopener,noreferrer' );
				} }>
					{ SVGS.SVG_CONTACT_ILLUSTRATION }
				</button>
				<h3 className="nextgen-modal__subtitle">{ __( 'Contact our GoDaddy guides to get started.', 'nextgen' ) }</h3>
			</div>
		),
		actions: {
			back: {
				label: __( 'Go Back', 'nextgen' ),
				action: () => props.setStep( 'options' ),
			},
			next: {
				label: __( 'Contact Us', 'nextgen' ),
				action: () => {
					props.sendFeedback( 'help' );
					props.closeEditorPreferenceModal();
					window.open( 'https://www.godaddy.com/help/contact-us', '_blank', 'noopener,noreferrer' );
				},
			},
		},
	},

	// Redirecting to Default (WordPress)
	redirectingDefault: {
		content: (
			<div className="editor-preference-modal__redirecting">
				{ SVGS.SVG_WORDPRESS }
				<p>{ __( 'Redirecting you to the WordPress Admin', 'nextgen' ) }</p>
			</div>
		),
	},

	// Redirecting to 3rd party plugins
	redirecting3rdParty: {
		content: (
			<div className="editor-preference-modal__redirecting">
				{ SVGS.SVG_WORDPRESS }
				<p>{ __( 'Redirecting you to WordPress plugins', 'nextgen' ) }</p>
			</div>
		),
	},
} );

export default defaultWorkflow;
